/* Header */
.header {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 999;
  padding: 1rem;
  width: 100%;
  height: var(--header-height);
  background-color: var(--white-color);
}

.header > .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo a {
  color: var(--first-color);
  font-weight: bold;
  font-size: 2rem;
}

.header__logo a:hover {
  opacity: 0.7;
}

/* Dark Theme Button */
.switch {
  display: inline-block;
  height: 34px;
  position: relative;
  width: 60px;
}

.switch input {
  display: none;
}

.slider {
  background-color: var(--text-color);
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
}

.nav__menu {
  position: fixed;
  right: 0;
  bottom: var(--header-height);
  width: 50%;
  display: flex;
  flex-direction: column;
  background-color: var(--white-color);
  opacity: 0;
  transition: transform 0.4s ease, opacity 0.2s ease;
  transform: translate(50%, 0);
  pointer-events: none;
}

.nav__menu.is-active {
  transform: translate(0, 0);
  opacity: 1;
  pointer-events: auto;
}

.nav__menu a {
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: bold;
  text-align: center;
  color: var(--first-color);
}

.active__link {
  color: var(--text-color) !important;
}

.nav__menu a:hover {
  color: var(--white-color);
  background-color: var(--first-color);
  opacity: 0.75;
}

/* La pelotita de color */
.slider:before {
  background-color: var(--first-color);
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position: absolute;
  transition: 0.4s;
  width: 26px;
}

/* Cuando se apreta la pelota se cambia el fondo */
input:checked + .slider {
  background-color: var(--text-color);
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/***** Menú *****/
.header__menu-btn {
  border: 0;
  cursor: pointer;
}

.hamburger {
  padding: 0;
}

.hamburger-inner,
.hamburger-inner:after,
.hamburger-inner:before {
  background-color: var(--first-color) !important;
}

@media screen and (min-width: 1024px) {
  .header {
    position: sticky;
    margin-top: 1.5rem;
    top: -0.1rem;
    padding: 0.5rem;
    height: calc(var(--header-height) - 0.5rem);
    border-bottom: thin solid var(--gray-light);
  }

  .header__menu-btn {
    display: none;
  }

  .nav__menu {
    position: static;
    opacity: 1;
    pointer-events: auto;
    width: auto;
    flex-direction: row;
    transform: none;
  }

  .nav__menu a {
    padding: 0 1rem;
  }

  .nav__menu a:hover {
    color: var(--title-color);
    background-color: transparent;
  }

  .nav__menu a:last-child {
    padding-right: 0;
  }
}
