/******* Propiedades Personalizadas *******/
:root {
  --first-color: #c28f2c;
  --white-color: #f8f8f8;
  --gray-dark-color: #666;
  --link-color: #181168;
  --text-color: #222;
  --font: "Raleway", sans-serif;
  --max-width: 1200px;
  --header-height: 4rem;
  --box-shadow: 0.25rem 0.25rem 1rem rgba(0, 0, 0, 0.25);
  --hero-text-color: #fff;
  --gray-light: #c7c7c7c7;
}

:root.dark-mode {
  --white-color: #1a1a1a;
  --gray-light: #2f2f2f;
  --text-color: #eee;
  --box-shadow: 0.25rem 0.25rem 1rem rgba(255, 255, 255, 0.25);
}

/****************************************** RESET  ***************************************/

html {
  box-sizing: border-box;
  font-family: var(--font);
  font-size: 16px;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  overflow-x: hidden;
  color: var(--text-color);
  background-color: var(--white-color);
}

a {
  color: var(--link-color);
  text-decoration: none;
  transition: all 0.5s ease-out;
}

h1 {
  font-size: calc(2rem + 3.03vw);
  color: var(--first-color);
  text-align: center;
  font-style: italic;
}

h2 {
  margin: 0;
  font-size: 1.5rem;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
}

h4 {
  margin: 0;
  font-size: 1rem;
}

h5 {
  margin: 0;
  font-size: 0.85rem;
}

h6 {
  margin: 0;
  font-size: 0.7rem;
}

img {
  max-width: 100%;
  height: auto;
}

p {
  line-height: 1.6;
}

main {
  background-color: var(--white-color);
}

.box-shadow-1 {
  box-shadow: var(--box-shadow);
}

::selection {
  background-color: var(--first-color);
  color: white;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: var(--max-width);
}

.contact__page {
  padding: 2rem 1rem;
}

.content__container {
  min-height: 100vh;
}

/* Input Champs */

.input__champs--container {
  position: relative;
  padding: 15px 0 0;
  margin: 0 auto 1rem;
  width: 20%;
}

.input__champs {
  width: 100%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: var(--first-color);
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.input__champs::placeholder {
  color: transparent;
}
.input__champs:placeholder-shown ~ .input__champs--label {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}

.input__champs--label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #9b9b9b;
}

.input__champs:focus {
  font-weight: 700;
  border-bottom: 2px solid #00b5e9;
}
.input__champs:focus ~ .input__champs--label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #00b5e9;
  font-weight: 600;
}
