.champions a {
  color: initial;
  margin-bottom: 1rem;
}

.champion__card {
  margin: 1rem 0;
  width: 100%;
  text-align: center;
  transition: all 0.3s ease-in-out;
  animation: fadein 2s;
  position: relative;
  overflow: hidden;
  text-align: left;
}

.champ__data {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #061c25bf;
  padding: 1rem;
  transition: background-color 0.3s ease-in-out;
}

.champion__card h3 {
  color: var(--hero-text-color);
  transition: color 0.3s ease-in-out, font-size 0.3s ease-in-out,
    transform 0.3s ease-in-out;
}

.champion__card img {
  border-radius: 2%;
  filter: grayscale(1);
  transition: filter 0.3s ease-in-out, transform 1s ease;
  height: 100%;
}

.champ__data p {
  font-weight: bold;
  font-size: 0.8rem;
  color: var(--hero-text-color);
  margin: 0;
}

.champion__card:hover {
  transform: scale(1.1);
}

.champion__card:hover > .champ__data h3 {
  color: var(--first-color);
  font-size: 1.4rem;
  transform: translateX(30px);
}

.champion__card:hover > img {
  filter: grayscale(0);
  transform: scale(1.08, 1.08);
}

.champion__card:hover .champ__data {
  background-color: #006680d1;
}

.filter {
  display: none;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
