.modal {
  position: fixed;
  display: flex;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: #00000075;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: opacity 1.3s ease-in-out;
}

.modal-container {
  position: relative;
  padding: 1rem;
  min-width: 320px;
  min-height: 200px;
  max-height: 600px;
  overflow-y: auto;
  background-color: var(--white-color);
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.modal.is-open {
  opacity: 1;
  pointer-events: auto;
}

.modal__content {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  h3 {
    color: var(--first-color);
    font-size: 2rem;
  }
  p {
    font-weight: bold;
  }
  img {
    grid-row: 1/3;
  }

  .modal__data {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 1rem;
    padding: 1rem 1rem 0 0;
  }

  .champ__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 1rem;
    grid-column: 1/3;
  }

  .modal__abilities--container {
    margin-left: 1rem;
  }

  .abilities__row {
    display: flex;

    div {
      border-radius: 50%;
    }

    img {
      border-radius: 50%;
    }
  }
}

@media screen and (min-width: 420px) {
  .modal__content {
    h3 {
      font-size: 3rem;
    }
    p {
      font-size: 1.2rem;
    }
    .champ__content {
      grid-column: 2/3;
    }
  }
}
