/* Footer */
.footer {
  margin-bottom: var(--header-height);
  background-color: var(--first-color);
  color: white;
  text-align: center;
  padding: 0.5rem;
  margin-top: auto;
}

.heart {
  animation: pulse 1s linear infinite;
  display: inline-block;
  position: relative;
  width: 14px;
  height: 10px;
}

.heart::after,
.heart::before {
  position: absolute;
  content: "";
  left: 7px;
  top: 0;
  width: 7px;
  height: 12px;
  background-color: var(--text-color);
  transform: rotate(-45deg);
  transform-origin: 0 100%;
  border-radius: 7px 7px 0 0;
}

.heart::after {
  left: 0;
  transform: rotate(45deg);
  transform-origin: 100% 100%;
}

@keyframes pulse {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (min-width: 1024px) {
  .footer {
    margin-bottom: 0;
  }
}
